async function setSlotTime(id,state) {
  
  const min = state.firstDate.getMinutes();
  const hour = state.firstDate.getHours();
  const minutes = min < 9 ? `0${min}` :`${min}`;
const hours = hour < 9 ? `0${hour}`:`${hour}`
const min2 = state.lastDate.getMinutes();
const hour2 = state.lastDate.getHours();
const minutes2 = min2 < 9 ? `0${min2}` :`${min2}`;
const hours2 = hour2 < 9 ? `0${hour2}`:`${hour2}`
  const options = {
    headers: { 
      "Content-Type": "application/json",
    }
  };
const { data: response } = await axios.post(
  URL_PREFIX + "/v1/dashboard/service/slot/time/update", 
  {
     service_id: id,
    time: `${hours}:${minutes}-${hours2}:${minutes2}`,
    slot_interval:state.intervals,
    min_slots:state.minSlot
  },
  options
);
return response;

}
